@import './colorsandfonts';



.my-nav {
    margin: 0px;
    padding: 0px;
    display: block;
    min-height: 100vh;
    //max-height: 100vh;
    background-color: $darkgrey;
    z-index: 1;
    
    .col-sm-12 {
        //border-bottom: 1px solid $beige;
        padding: 0px;
    }
}

    
.my-logo {
    height: 150px;
    padding: 0px;
    //background-image: url("../../images/logo/logo192.png");
    //background-size: 100%;
    //background-position: center;
    //background-repeat: no-repeat;
    background-color: $black; 
    display: flex;
    
    img {
        height: 65%;
        margin: auto;  /* Magic! */
    }
}

/*Mobile*/
@media only screen and (max-width: 600px) {
    .my-nav {
        min-height: 10vh;
        max-height: 10vh;
        
        .col-sm-12 {
            max-width: 10vh;
            max-height: 10vh;
            
            .my-logo {
                height: 10vh;    
            }
        }
    }
}