/*Animations*/
    /*Typing*/
.typing {    
    white-space: nowrap;
    overflow: hidden;
}

@keyframes type {
  0% {
    width: 0;
    border-right: .15em solid #0a9de3;
  }
  99.9% {
    border-right: .15em solid #0a9de3;
  }
  100% {
    border: none;
  }
}

@-webkit-keyframes type {
  0% {
    width: 0;
    border-right: .15em solid #0a9de3;
  }
  99.9% {
    border-right: .15em solid #0a9de3;
  }
  100% {
    border: none;
  }
}

@keyframes visible-delay {
  99% {
    visibility: hidden;
  }
  100% {
    visibility: visible;
  }
}
