@import './colorsandfonts';

.loading.container-fluid {
    margin:0px;
    padding: 55px;
    padding-top: 110px;
    padding-bottom: 0px;
    min-height: 100vh;
    width: auto;
    color: #fff;    
    height: 100%;
    width: 100%;
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    background-color: #000;
}

.loading.container-fluid .my-loading {
    display: block;
    margin-left: 35%;
    margin-top: 15%;
    -webkit-animation: 0.5s hide-delay 2.6s steps(5, end);
    animation:0.5s hide-delay 2.6s steps(5, end);
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;  
    opacity: 1;
}

.loading.container-fluid .my-loading p {
   font-family: $code;
    font-style: normal;
    font-weight: 300;
    color: #fff;
    font-size: 16px;
    line-height:32px;
    margin: 0;
    padding: 0;

}

/*Animations*/
    /*Loading*/
@keyframes hide-delay {
  0% {
      opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
    /*Typing*/
.typing1 {
    width: 200px;    
    -webkit-animation: 0.5s type 0.5s steps(5, end),0.65s visible-delay;
    animation: 0.5s type 0.5s steps(5, end),0.65s visible-delay;
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;  
    visibility: hidden;
}

.typing2 {
    width: 240px;    
    -webkit-animation: 0.5s type 1s steps(5, end),1.15s visible-delay;
    animation: 0.5s type 1s steps(5, end),1.15s visible-delay;
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;  
    visibility: hidden;
}

.typing3 {
    width: 340px;    
    -webkit-animation: 1s type 1.5s steps(10, end),1.65s visible-delay;
    animation: 1s type 1.5s steps(10, end),1.65s visible-delay;
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
    visibility: hidden;
}

.typing4 {
    width: 30px;    
    -webkit-animation: 0.1s type 2.5s steps(1, end),2.65s visible-delay;
    animation: 0.1s type 2.5s steps(1, end),2.65s visible-delay;
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
    visibility: hidden;
}

/*Mobile*/
@media only screen and (max-width: 600px) {
    .loading.container-fluid .my-loading {
        display: block;
        margin-left: 15%;
        margin-top: 75%;
    }  
    
    .loading.container-fluid .my-loading p {
        font-size: 8px;
        line-height:16px;         
    }
    
    .typing1 {
        width: 100px;  
        
    }
    
    .typing2 {
        width: 120px;  
        
    }
    
    .typing3 {
        width: 170px;  
        
    }
    
    .typing4 {
        width: 10px;  
        
    }
}