@import './components/styles/colorsandfonts';

html {
    scroll-behavior: smooth;
    cursor: default;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    margin: 0px;
    padding: 0px;
    //overflow: hidden;
    color: #fff;
    background-color: $black;
}

h1 {
    font-family: $h1;
    color: $yellow;
    font-weight: 900;
    font-size: 88px;
    line-height: 80px;
    position: relative;
    margin-bottom: 48px;
    
    &:before {
        content: '<h1>';
        font-family: $tag;
        color: #515152;
        font-size: 18px;
        font-weight: 100;
        position: absolute;
        top: -55px;
        left: -10px;
    }
    
    &:after {
        content: '</h1>';
        font-family: $tag;
        color: #515152;
        font-size: 18px;
        font-weight: 100;
        position: absolute;
        left: -10px;
        bottom: -65px;
    }
}

h2 {
    font-family: $h2;
    color: $green;
    font-weight: 900;

    font-size: 34px;
    line-height: 26px;
    position: relative;
    margin-bottom: 48px;
    
    &:before {
        content: '<h2>';
        font-family: $tag;
        color: #515152;
        font-size: 18px;
        font-weight: 100;
        position: absolute;
        top: -25px;
        left: -10px;
    }
    
    &:after {
        content: '</h2>';
        font-family: $tag;
        color: #515152;
        font-size: 18px;
        font-weight: 100;
        position: absolute;
        left: -10px;
        bottom: -35px;
    }
}

h3 {
    font-family: $h3;
}

h4 {
    font-family: $h4;
}

h5 {
    font-family: $h5;
}

h6 {
    font-family: $h6;
}

p {
    font-family: $p;
    position: relative;
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-size: 16px;
}

a {
    font-family: $a;
    font-size: 16px;

}

code {
  font-family: $code;
}

.code {
  font-family: $code;
}

.text-zone {
    margin-bottom: 48px;
    
    p {
    
        &:first-of-type:before {
            content: '<p>';
            font-family: $tag;
            color: #515152;
            font-size: 18px;
            font-weight: 100;
            position: absolute;
            top: -25px;
            left: -10px;
        }

        &:last-of-type:after {
            content: '</p>';
            font-family: $tag;
            color: #515152;
            font-size: 18px;
            font-weight: 100;
            position: absolute;
            left: -10px;
            bottom: -25px;
        }    
    }    
}

.image-section-before::before {
        content: '<section>';
        font-family: $tag;
        color: #515152;
        font-size: 18px;
        font-weight: 100;
        position: absolute;
        top: -25px;
        left: -10px;
    }

.image-section-after::after {
        content: '</section>';
        font-family: $tag;
        color: #515152;
        font-size: 18px;
        font-weight: 100;
        position: absolute;
        left: -10px;
        bottom: -35px;
}

.big-text {
    font-size: 128px;
    line-height: 120px;
    color: $beige;
    opacity: 0.25;
    top: -11px;
    left: 0px;
    z-index: 0;
    position: absolute;
    &:before, 
    &:after {
        content: '';
    }
}

.no-bottom-margin {
    margin-bottom: 0px !important;
}

.col-sm-1,
.col-sm-2,
.col-sm-3,
.col-sm-4,
.col-sm-5,
.col-sm-6,
.col-sm-7,
.col-sm-8,
.col-sm-9,
.col-sm-10,
.col-sm-11,
.col-sm-12 {
    position: relative;
    //border: 1px solid $white;
}

.mobile-only {
    display: none;
}

/*Mobile*/
@media only screen and (max-width: 600px) {
    html,
    body {
        overflow: auto;
    }
    
    .mobile-only {
        display: block;
    }
    
    .big-text {
        display: none;
    }
    
    h1 {
        font-size: 80px;
        line-height: 80px;
    }
    
    .big-text {
        font-size: 40px;
        line-height: 36px;
    }
    
    .image-section-before::before {
        top: -25px;
        left: 15px;
    }
    
    .image-section-after::after {
        left: 15px;
        bottom: -15px;
    }
}
