@import './colorsandfonts';

.my-home {
    margin: 0px;
    padding: 0px;
    display: block;
    min-height: 100vh;
    background-color: $lightgrey;

    .row {  
        margin: 0px;
        padding: 0px;
        
        .col-sm-4 {
            margin: 0px;
            padding: 25px;
            padding-bottom: 0px;
            display: block;
            min-height: 50vh;
            //max-height: 50vh;
        }
        
        .col-sm-8 {
            margin: 0px;
            padding: 25px;
            padding-bottom: 0px;
            display: block;
            min-height: 50vh;
            //max-height: 50vh;
        }
        
        .col-sm-6 {
            margin: 0px;
            padding: 25px;
            padding-bottom: 0px;
            display: block;
            min-height: 50vh;
            //max-height: 50vh;
        }
    }
}

.my-icons {
    margin-top: 80px;
    padding: 30px;
    .icon {
        height: 50px;
        display: inline-block;
        padding: 10px;
        z-index: 1;
        position: relative;
    }
}

.image-section {
    padding: 0px;
    margin: 0px;
}

.my-projects {
    position: relative;
    left: 80px;
    top: 20px;   

    .image-flip {
        display: inline-block;
        //position: fixed;
        display: block;
        height: 33.33vh;
        width: 33.33vh;
        background: $black;

    }
    
    .image-flip:nth-child(1) {
        background-image: url('../../images/projects/jackandcharlie.png');
        background-position: center;
        background-repeat: no-repeat;
        background-size: 100%;        
        -webkit-transition: background-size .2s ease-in;
        transition: background-size .2s ease-in;
        z-index: 1;      
        &:hover {
            background-size: 120%;
        }
    }
        
    .image-flip:nth-child(2) {
        background-image: url('../../images/projects/newera.png');
        background-position: center;
        background-repeat: no-repeat;
        background-size: 100%;   
        //margin-left: 33.33vh;
        -webkit-transition: background-size .2s ease-in;
        transition: background-size .2s ease-in;
        z-index: 1;
        &:hover {
            background-size: 120%;
        }
    }
    
    .image-flip:nth-child(3) {
        background-image: url('../../images/projects/tilesandbeyond.png');
        background-position: center;
        background-repeat: no-repeat;
        background-size: 100%; 
        //margin-left: 33.33vh*2;
        -webkit-transition: background-size .2s ease-in;
        transition: background-size .2s ease-in;
        z-index: 1;
        &:hover {
            background-size: 120%;
        }
    }

}

/*Mobile*/
@media only screen and (max-width: 600px) {
    .my-projects {
        left: -0px;
        top: 00px;
    }
    
    .my-icons {
        padding-top: 0px;
    }
    
    .image-flip { 
        width: 100vw !important;
        height: 100vw !important;

    }
    
    .my-home .col-sm-8 {
        padding: 0px !important;
        padding-bottom: 20px !important;
        
        h2 {
            margin-top: 25px;
            margin-left: 25px;
        }
    }
    
    .my-home .col-sm-6:nth-child(1) {
        padding-top: 50px;
    }

}